.footer {
    margin-top: auto;
    padding: 25px 0;
    background-color: black;
    color: var(--footer-text);
}

.footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__content {
    display: flex;
    align-items: center;
    gap: 15px; /* Space between the text and VK logo */
}

.footer__text {
    font-size: 14px;
    font-weight: 400;
    color: var(--footer-text);
    transition: color 0.3s ease;
}

.footer__text:hover {
    color: #8d8d8d;
}

.social__link {
    display: inline-block;
}

.social__icon {
    width: 20px;
    height: 20px;
    transition: transform 0.5s ease;
    filter:invert(1);
}

.social__icon:hover {
    transform: translateX(0px) rotate(360deg);
}